export default{
	
	data(){
		return{
			
			page:1,
			loadingText: '正在加载数据...',
		}
	},
	onReachBottom() {
	    this.page++;
	    this.loadingText='正在加载更多数据...';
	    this.getServicList();
	},
	onPullDownRefresh() {
		this.getServicList();
	},
	onLoad(eee) {
		
		var _this = this;
		uni.getStorage({
			key: 'mid',
			success: function(res) {
				console.log('getStoragegemid' + res.data);
				_this.theMid = res.data;
				// _this.getServicList();
			}
		});
		// this.getServicList();
	},
	methods:{
		
		async getServicList(){
			
			var serverUrl="Home/Message/getList";
			var data={
				mid:this.theMid,
				type:this.currentIndex,
				limit:10,
			    page:this.page
			}
			var res = await this.requestFromServer({
				url:serverUrl,
				data:data
			})
			if(this.page==1){
				this.showNewsData=[];
			}
			uni.stopPullDownRefresh();
			if(res.data){
				this.showNewsData=this.showNewsData.concat(res.data);
				if(res.data.length<10){
					this.loadingText='没有更多数据了';
				}else{
					this.loadingText='加载更多数据...';
				}
			}else{
				this.loadingText='没有更多数据了';
			}
			
		},
		setDefault(item){
			
		},
		del(){
			uni.showModal({
				title: '提示',
				content: '确定删除?',
				success: function(res) {
					if (res.confirm) {
						console.log('点击确定');
						
								
					}
				}
			})
		},
		addadd(){
		    uni.navigateTo({
		    	url:'./addAddress'
		    })	
		},
		edit(){
		    uni.navigateTo({
		    	url:'./addAddress'
		    })	
		},
		
		dateFormat:function(time) {
			return this.getDateSecond(time);
		},
	}
}
